<template>
	<div>
		<header-nav></header-nav>
		<div class="wrap">
			<div class="timg-box">
	          <img src="/static/timg.gif" alt="" class="timg" v-show="!data">
	        </div>
			<h2>{{$route.params.id}}</h2>
			<ul v-show="data">
		        <li v-for="item in data">
		          <router-link :to="{path:'/one',query:{id:item.id}}">
			          <img :src="item.images.medium" alt="">
			          <p class="title">{{item.title}}</p>
			          <div v-show="item.rating.average==0" class="gray">暂无评分</div>
			          <div v-show="!item.rating.average==0">
			            <div class="kong">
			              <div :style="{width:item.rating.average*10+'%'}" class="shi"></div>
			            </div><span class="num">{{item.rating.average}}</span>
			          </div>
		          </router-link>
		        </li>
		    </ul>
		</div>
	</div>
</template>
<script>
import headerNav from '@/components/headerNav'
export default {
	name:'classify',
	data(){
		return {
			data:null,
		}
	},
	components:{
		headerNav
	},
	mounted(){
		this.$http.jsonp('https://api.douban.com/v2/movie/search?tag='+this.$route.params.id).then(function(res){
            this.data=res.data.subjects;
        },function(err){
            console.log('sb')
        })
	}
	
}
</script>
<style scoped>
.wrap{
	margin-top: 1.2rem;
}
.timg-box{
  width:100vw;
  position: absolute;
  top:50%;
  transform: translateY(-50%);
  text-align: center;
}
.timg{
  width:2rem;
  height:2rem;
}
ul {
	margin:0;
  list-style-type: none;
  padding:0 .2rem;
}
h2{
	font-size:.32rem;
	padding:0 .2rem;
}
li {
  width:2rem;
  display: inline-block;
  margin: .2rem .15rem;
  text-align: center;
  font-size:.26rem;
}
.title{
    width:100%;
    margin:.24rem 0 .1rem;
    word-break:keep-all;
    white-space:nowrap;
    overflow:hidden;
    text-overflow:ellipsis;
}
li img{
	width:1.8rem;
	height:2.3rem;
}
.gray{color:#999;}
.kong{width:1.1rem;height:.21rem;background: url(../../static/kong.png);display: inline-block;}
.shi{width:100%;height:.21rem;background: url(../../static/shi.png);vertical-align: middle;}
.num{margin:0 0 0 .2rem;vertical-align: middle;color:#999;}
a{
	color:black;
	text-decoration:none;
}
</style>


