<template>
	<div>
		<header-nav></header-nav>
		<div class="wrap">
			<div class="timg-box">
	          <img src="/static/timg.gif" alt="" class="timg" v-show="!datalis.length">
	        </div>
			<h2>{{title}}</h2>
			<ul v-show="datalis.length">
		        <li v-for="item in datalis">
		          <router-link :to="{path:'/one',query:{id:item.id}}">
			          <img :src="item.images.medium" alt="">
			          <p class="title">{{item.title}}</p>
			          <div v-show="item.rating.average==0" class="gray">暂无评分</div>
			          <div v-show="!item.rating.average==0">
			            <div class="kong">
			              <div :style="{width:item.rating.average*10+'%'}" class="shi"></div>
			            </div><span class="num">{{item.rating.average}}</span>
			          </div>
		          </router-link>
		        </li>
		     </ul>
		</div>
	</div>
</template>
<script>
import headerNav from '@/components/headerNav'
export default{
	data(){
		return {
			data:[
				'https://api.douban.com/v2/movie/in_theaters',
				'https://api.douban.com/v2/movie/us_box',
				'https://api.douban.com/v2/movie/top250',
			],
			datalis:[],
			title:'',	
		}
	},
	mounted:function(){
		if (this.$route.query.id==0) {
	    	this.aa();
		}else if(this.$route.query.id==1){
	    	this.bb();
		}else{
	    	this.cc();	
		}
	  },
	methods:{
		aa(){
	        this.$http.jsonp(this.data[0]).then(function(res){
	        	this.title=res.data.title;
	            this.datalis=res.data.subjects;
	            // console.log(this.datalis.title)
	          },function(err){
	            console.log('sb')
	          })
	      },
	      bb(){
	        this.$http.jsonp(this.data[1]).then(function(res){
	        	this.title=res.data.title;
	        	for (var i = 0; i < 10; i++) {
	            	this.datalis.push(res.data.subjects[i].subject);
	        	}
	            // console.log(res.data)
	          },function(err){
	            console.log('sb')
	          })
	      },
	      cc(){
	        this.$http.jsonp(this.data[2]).then(function(res){
	        	this.title=res.data.title;
	            this.datalis=res.data.subjects;
	          },function(err){
	            console.log('sb')
	          })
	      },
	},
	components:{
		headerNav
	}
}
</script>
<style scoped>
.wrap{
	margin-top: 1.2rem;
}
.timg-box{
  width:100vw;
  position: absolute;
  top:50%;
  transform: translateY(-50%);
  text-align: center;
}
.timg{
  width:2rem;
  height:2rem;
}
ul {
	margin:0;
  list-style-type: none;
  padding:0 .2rem;
}
h2{
	font-size:.32rem;
	padding:0 .2rem;
}
li {
  width:2rem;
  display: inline-block;
  margin: .2rem .15rem;
  text-align: center;
  font-size:.26rem;
}
.title{
    width:100%;
    margin:.24rem 0 .1rem;
    word-break:keep-all;
    white-space:nowrap;
    overflow:hidden;
    text-overflow:ellipsis;
}
li img{
	width:1.8rem;
	height:2.3rem;
}
.gray{color:#999;}
.kong{width:1.1rem;height:.21rem;background: url(../../static/kong.png);display: inline-block;}
	.shi{width:100%;height:.21rem;background: url(../../static/shi.png);vertical-align: middle;}
.num{margin:0 0 0 .2rem;vertical-align: middle;color:#999;}
a{
	color:black;
	text-decoration:none;
}
</style>


