import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import Hot from '@/components/hot'
import One from '@/components/detail'
import Search from '@/components/search'
import Book from '@/components/book'
import Bookmore from '@/components/bookmore'
import Bookdetails from '@/components/details'
import Music from '@/components/music'
import Musicmore from '@/components/musicmore'
import Musicdetails from '@/components/detailss'
import Group from '@/components/group'
import classify from '@/components/classify'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    { path: "*", redirect: '/movies' },
    {
      path: '/movies',
      name: 'Hello',
      component: Hello
    },
    {
      path: '/hot',
      name:'name',
      component: Hot
    },
    {
      path: '/one',
      component: One
    },
    {
      path: '/search',
      component: Search
    },
    {
      path: '/book',
      component: Book
    },
    {
      path: '/bookmore/:id',
      component: Bookmore
    },
    {
      path: '/bookdetails/:id',
      name: 'details',
      component: Bookdetails
    },
    {
      path: '/music',
      component: Music
    },
    {
      path: '/musicmore/:id',
      component: Musicmore
    },
    {
      path: '/musicdetails/:id',
      name: 'detailss',
      component: Musicdetails
    },
    {
      path: '/group',
      component: Group
    },
    {
      path: '/classify/:id',
      name: 'classify',
      component: classify
    },
  ]
})



