<template>
	<div class="wrap">
		<nav class="header">
			<router-link tag="label" to="/movies" @click="back">关闭</router-link>
			<input type="text" v-model="inptext" @keyup.enter="gg" placeholder="输入要找的电影/图书/音乐">
			<button @click="gg">搜索</button>
		</nav>
		<div v-show="!search">
			<ul>
				<li v-for="item in datalis">
					<h2 :style="{color:item.color}">{{item.font}}</h2>
					<p>{{item.smallfont}}</p>
				</li>
			</ul>
			<div class="box">
				<div>
					<span>注册账号</span>
					<span>登录豆瓣</span>
				</div>
				<div>
					<span>使用桌面版</span>
					<span>使用豆瓣App</span>
				</div>
			</div>
		</div>
		<div v-show="search">
			<h2 class="tit">相关电影</h2>
			<div v-for="item in search.subjects" class="result">
				<router-link :to="{path:'/one',query:{id:item.id}}">
					<img :src="item.images.medium" alt="" class="img">
					<div class="small-box">
						<h5>{{item.title}}</h5>
						<div v-show="item.rating.average==0" class="wu">评分人数不足</div>
						<div v-show="!item.rating.average==0">
							<div class="kong wu">
					            <div :style="{width:item.rating.average*10+'%'}" class="shi"></div>
					        </div><span class="num">{{item.rating.average}}</span>
						</div>
					</div>
				</router-link>
			</div>
			<h2 class="tit">相关书籍</h2>
			<div v-for="item in searchbook.books" class="result">
				<router-link :to="{name:'details',params:{id:item.id}}">
					<img :src="item.images.medium" alt="" class="img">
					<div class="small-box">
						<h5>{{item.title}}</h5>
						<div v-show="parseInt(item.rating.average)==0" class="wu">评分人数不足</div>
						<div v-show="!parseInt(item.rating.average)==0">
							<div class="kong wu">
					            <div :style="{width:item.rating.average*10+'%'}" class="shi"></div>
					        </div><span class="num">{{item.rating.average}}</span>
						</div>
					</div>
				</router-link>
			</div>
			<h2 class="tit">相关音乐</h2>
			<div v-for="item in searchmusic.musics" class="result">
				<router-link :to="{name:'detailss',params:{id:item.id}}">
					<img :src="item.image" alt="" class="img">
					<div class="small-box">
						<h5>{{item.title}}</h5>
						<div v-show="parseInt(item.rating.average)==0" class="wu">评分人数不足</div>
						<div v-show="!parseInt(item.rating.average)==0">
							<div class="kong wu">
					            <div :style="{width:item.rating.average*10+'%'}" class="shi"></div>
					        </div><span class="num">{{item.rating.average}}</span>
						</div>
					</div>
				</router-link>
			</div>
		</div>
	</div>
</template>
<script>
export default {
	data(){
		return {
				datalis:[
					{
						smallfont:'影院热映',
						font:'电影',
						color:'rgb(35, 130, 232)',
					},
					{
						smallfont:'正在热播',
						font:'电视',
						color:'rgb(122, 106, 219)',
					},
					{
						smallfont:'畅销排行',
						font:'图书',
						color:'rgb(159, 120, 96)',
					},
					{
						smallfont:'周末活动',
						font:'同城',
						color:'rgb(230, 70, 126)',
					},
					{
						smallfont:'志趣相投',
						font:'小组',
						color:'rgb(42, 184, 204)',
					},
					{
						smallfont:'新碟榜',
						font:'音乐',
						color:'rgb(244, 143, 46)',
					},
					{
						smallfont:'电子书',
						font:'阅读',
						color:'rgb(159, 120, 96)',
					},
					{
						smallfont:'虚拟世界',
						font:'游戏',
						color:'rgb(87, 116, 197)',
					},
					{
						smallfont:'玩手机',
						font:'应用',
						color:'rgb(89, 108, 221)',
					},
					{
						smallfont:'心爱之物',
						font:'东西',
						color:'rgb(225, 100, 77)',
					},
					{
						smallfont:'红心歌单',
						font:'FM',
						color:'rgb(64, 207, 169)',
					},
					{
						smallfont:'购买原创',
						font:'市集',
						color:'rgb(66, 189, 86)',
					},
				],
				inptext:'',
				search:'',
				searchbook:'',
				searchmusic:'',
		}
	},
	methods:{
		gg(){
			console.log(this.inptext)
			this.$http.jsonp('https://api.douban.com/v2/movie/search?tag='+this.inptext).then(function(res){
				this.search=res.data;
			},function(err){
				console.log('sb')
			});
			this.$http.jsonp('https://api.douban.com/v2/book/search?q='+this.inptext).then(function(res){
				this.searchbook=res.data;
			},function(err){
				console.log('sb')
			});
			this.$http.jsonp('https://api.douban.com//v2/music/search?tag='+this.inptext).then(function(res){
				this.searchmusic=res.data;
			},function(err){
				console.log('sb')
			});
			this.inptext='';
		},

		back(){
			window.history.go(-1);
		}
	}
}
</script>
<style scoped>
	.header{
		height:.5rem;
		padding:.3rem .2rem;
		border-bottom:1px solid #999;
		font-size:.24rem;
	}
	label{
		color:#42bd56;
		font-size:.3rem;
	}
	.header input{
		height:100%;
		width:60%;
		padding:0 .2rem;
		margin-left:.2rem;
		background: #eee;
		border:none;
		font-size: .24rem;
		vertical-align: middle;
	}
	button{
		height:100%;
		font-size: .24rem;
		border:none;
		background: transparent;
		vertical-align: middle;
		outline: none;
	}
	ul{
		padding:0 .2rem;
		text-align:center;
	}
	li{
		display: inline-block;
		width:30%;
		margin:.1rem;
		list-style: none;
		font-size:.24rem;
	}
	li h2{
		margin:.1rem 0;
	}
	li p{
		color:#999;
	}
	p{margin:0;}
	.box{
		margin-top: .5rem;
		font-size:.28rem;
		text-align: center;
		color:#42bd56;
	}
	.box div{
		padding:.3rem 0;
	}
	.box div:nth-of-type(1){
		border-top:1px solid #ccc;
		border-bottom:1px solid #ccc;
	}
	.box div:nth-of-type(2){
		border-bottom:1px solid #ccc;
	}
	.box div span{
		display:inline-block;
		width:45%;
	}
	.box div span:nth-of-type(1){
		border-right:1px solid #ccc;
	}
	.tit{
		font-size:.3rem;
		margin:.1rem .3rem;
	}
	.result{
		margin:.2rem 0;
		font-size:.3rem;
	}
	.small-box{
		display: inline-block;
		padding:0 .1rem;
		color:#999;
	}
	.wu{margin-top:.2rem;}
	.img{
		width:1rem;
		margin:0 .2rem;
		box-shadow:.04rem .04rem .04rem #999;
		vertical-align: top;
	}
	h5{
		width:60vw;
		white-space: nowrap;
		overflow:hidden;
		text-overflow: ellipsis;
		margin:0;
		margin-bottom:.2rem;
		font-weight: 400;
		color:#000;
	}
	.kong{width:1.1rem;height:.21rem;background: url(../../static/kong.png);display: inline-block;}
	.shi{width:100%;height:.21rem;background: url(../../static/shi.png);vertical-align: middle;}
	.num{margin:0 0 0 .2rem;font-size: .24rem;vertical-align: middle;}
	.wu{font-size: .24rem;margin-top: 0;}
	a{
		text-decoration: none;
	}
</style>


