<template>
	<div class="wrap">
		<header-nav></header-nav>
		<div class="timg-box">
	        <img src="/static/timg.gif" alt="" class="timg" v-show="datalis.length">
	    </div>
		<div v-show="!datalis.length">
			<div class="bg">
				<img src="/static/bg.jpg" alt="">
				<div class="btn">
					<button><a href="https://www.douban.com/doubanapp/app?model=B&copy=1&page=movie&channel=card_movie&direct_dl=1">极速下载</a></button>
					<button>打开</button>
				</div>
			</div>
			<h2>{{datalis.title}}</h2>
			<div class="box">
				<div class="left">
					<div v-show="datalis.rating.average==0">暂无评分<em>{{datalis.ratings_count}}人评价</em></div>
					<div v-show="!datalis.rating.average==0">
						<div class="kong">
				            <div :style="{width:datalis.rating.average*10+'%'}" class="shi"></div>
				        </div><span class="num">{{datalis.rating.average}}<em>{{datalis.ratings_count}}人评价</em></span>
					</div>
					<p>	<span v-for="item in datalis.countries">{{item}}/</span>
						<span v-for="item in datalis.genres">{{item}}/</span>
						<span v-for="item in datalis.directors">{{item.name}}(导演)/</span>
						<span v-for="item in datalis.casts">{{item.name}}/</span>
						<span>{{datalis.year}}年/</span>
					</p>
					<p class="app">用App查看影人资料</p>
				</div>
				<div class="right">
					<img :src="datalis.images.medium" alt="">
				</div>
			</div>
			<div class="look">
				<span>想看</span>
				<span>看过</span>
			</div>
			<div class="box1">
				<h3 class="gray">{{datalis.title}}的剧情简介</h3>
				<p>{{datalis.summary}}</p>
			</div>
			<div>
				<h4 class="gray">影人</h4>
				<ul>
					<li v-for="item in datalis.directors">
						<img :src="item.avatars.medium" alt="">
						<p class="idd">{{item.name}}</p>
						<p class="id">导演</p>
					</li>
					<li v-for="items in datalis.casts">
						<img :src="items.avatars.medium" alt="">
						<p class="idd">{{items.name}}</p>
						<p class="id">演员</p>
					</li>
				</ul>
			</div>
		</div>
	</div>
</template>

<script>
import headerNav from '../components/headerNav'
export default {
	data(){
		return {
			id:this.$route.query.id,
			datalis:[]
		}
	},
	mounted:function(){
		this.get()
	},
	methods:{
		get(){
			this.$http.jsonp('https://api.douban.com/v2/movie/subject/'+this.$route.query.id).then(function(res){
				console.log(res.data)
				this.datalis=res.data
			},function(){
				console.log('sb')
			})
		}
	},
	components:{
		headerNav,
	}
}
</script>
<style scoped>
	.gray{
		color:#999;
	}
	.wrap{
		overflow: hidden;
		font-size:.26rem;
	}
	.timg-box{
	  width:100vw;
	  position: absolute;
	  top:50%;
	  transform: translateY(-50%);
	  text-align: center;
	}
	.timg{
	  width:2rem;
	  height:2rem;
	}
	.bg{
		position: relative;
		margin-top:.8rem;
	}
	.bg img{
		position: relative;
		height:1.5rem;
		width:100%;
	}
	.bg .btn{
		position: absolute;
		width:100vw;
		/*margin:.25rem 0;*/
		top:40%;
		text-align: right;
	}
	.bg button{
		height:.5rem;
		margin:0 .08rem;
		font-size:.26rem;
		border-radius:.1rem;
		border:none;
		outline:none;
	}
	.bg button:nth-of-type(1){
		width:1.2rem;
		background: #42bd56;
		color:white;
		font-weight: 900;
	}
	.bg button:nth-of-type(2){
		width:1rem;
		margin-right:.3rem;
		border:1px solid #42bd56;
		color:#42bd56;
		background: transparent;
	}
	h2,h3{
		font-weight: 400;
	}
	h2{
		font-size:.28rem;
		padding:0 .2rem;
	}
	.box{
		padding:0 .1rem;
		font-size:.26rem;
	}
	.kong{width:1.1rem;height:.21rem;background: url(../../static/kong.png);display: inline-block;}
	.shi{width:100%;height:.21rem;background: url(../../static/shi.png);vertical-align: middle;}
	.num{margin:0 0 0 .2rem;vertical-align: middle;}
	em{margin:0 .2rem;color:#999;}
	.app{color:#2fc5c5;}
	.box .left{
		width:3.5rem;
		float:left;
		padding:0 .2rem;
	}
	.box .right{
		padding-right:.45rem;
		float:right;
	}
	.box .right img{
		height:2rem;
		width:1.5rem;
	}
	.box:after{
		content: '';
		display: block;
		clear:both;
	}
	.look{
		text-align: center;
	}
	.look span{
		display: inline-block;
	    height: .5rem;
	    width:42vw;
	    line-height: .5rem;
	    border:1px solid #ffb712;
	    border-radius:.03rem;
	    margin-right: .1rem;
	    color: #ffb712;
	    text-align: center;
	}
	.box1{
		font-size:.24rem;
		padding:0 .2rem;
	}
	.box1 h3{font-size: .26rem}
	.box1 p{font-size: .24rem}
	h4{padding:0 .2rem;font-size: .26rem}
	ul{
		width:100vw;
		white-space: nowrap;
		overflow-x: auto;
		padding:0;
	}
	li{
		list-style: none;
		display: inline-block;
		text-align: center;
		margin:.1rem .2rem;
		font-size: .26rem;
	}
	.idd{margin:.24rem 0 0 0;font-size: .26rem;}
	.id{margin:.1rem .24rem 0;font-size: .26rem;}
	a{
		color:white;
		text-decoration: none;
	}
</style>


