<template>
  <div>
    <header-nav></header-nav>
    <div class="hello">
      <div class="hot">
        <h2>名著<router-link to="/bookmore/名著"><span>更多</span></router-link></h2>
        <div class="timg-box">
          <img src="/static/timg.gif" alt="" class="timg" v-show="!hots">
        </div>
        <ul>
          <li v-for="item in hots.books">
              <router-link :to="{name:'details',params:{id:item.id}}">
                <img :src="item.images.medium" alt="">
                <p class="title">{{item.title}}</p>
                <div v-show="parseInt(item.rating.average)==0" class="gray">暂无评分</div>
                <div class="xin-box" v-show="!parseInt(item.rating.average)==0">
                  <div class="kong">
                    <div :style="{width:item.rating.average*10+'%'}" class="shi"></div>
                  </div><span class="num">{{item.rating.average}}</span>
                </div>
              </router-link>
          </li>
        </ul>
      </div>
      <div class="free">
        <h2>网络小说<router-link to="/bookmore/网络小说"><span>更多</span></router-link></h2>
        <div class="timg-box">
          <img src="/static/timg.gif" alt="" class="timg" v-show="!frees">
        </div>
        <ul>
          <li v-for="item in frees.books">
              <router-link :to="{name:'details',params:{id:item.id}}">
                <img :src="item.images.medium" alt="">
                <p class="title">{{item.title}}</p>
                <div v-show="parseInt(item.rating.average)==0" class="gray">暂无评分</div>
                <div class="xin-box" v-show="!parseInt(item.rating.average)==0">
                  <div class="kong">
                    <div :style="{width:item.rating.average*10+'%'}" class="shi"></div>
                  </div><span class="num">{{item.rating.average}}</span>
                </div>
              </router-link>
          </li>
        </ul>
      </div>
      <div class="new">
        <h2>前端技术<router-link to="/bookmore/前端技术"><span>更多</span></router-link></h2>
        <div class="timg-box">
          <img src="/static/timg.gif" alt="" class="timg" v-show="!news">
        </div>
        <ul>
          <li v-for="item in news.books">
              <router-link :to="{name:'details',params:{id:item.id}}">
                <img :src="item.images.medium" alt="">
                <p class="title">{{item.title}}</p>
                <div v-show="parseInt(item.rating.average)==0" class="gray">暂无评分</div>
                <div class="xin-box" v-show="!parseInt(item.rating.average)==0">
                  <div class="kong">
                    <div :style="{width:item.rating.average*10+'%'}" class="shi"></div>
                  </div><span class="num">{{item.rating.average}}</span>
                </div>
              </router-link>
          </li>
        </ul>
      </div>
    </div>
  </div>
  
</template>

<script>
import headerNav from '@/components/headerNav'
export default {
  name: 'hello',
  data () {
    return {
        hots:'',
        frees:'',
        news:'',
    }
  },
  mounted:function(){
    this.aa();
    this.bb();
    this.cc();
    console.log('想看代码吗？求我啊')
  },
  methods:{
      aa(){
        this.$http.jsonp('https://api.douban.com/v2/book/search?q=名著').then(function(res){
            console.log(res.data)
            this.hots=res.data;
          },function(err){
            console.log('sb')
          })
      },
      bb(){
        this.$http.jsonp('https://api.douban.com/v2/book/search?q=网络小说').then(function(res){
            this.frees=res.data;
          },function(err){
            console.log('sb')
          })
      },
      cc(){
        this.$http.jsonp('https://api.douban.com/v2/book/search?q=前端技术').then(function(res){
            this.news=res.data;
           
          },function(err){
            console.log('sb')
          })
      },
  },
  components:{
    headerNav
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hello{
  margin-top: 1.2rem;
  padding:0 .2rem;
  font-size: .28rem;
}
h2{
  font-weight:400;
  font-size:.32rem;
}
h2 span{
  float:right;
  line-height:.47rem;
  color:#42bd56;
}
.gray{color:#999;}
.timg-box{
  width:100vw;
  text-align: center;
}
.timg{
  width:2rem;
  height:2rem;
}
ul {
  list-style-type: none;
  padding: 0;
  white-space: nowrap;
  width:100%;
  overflow-x: auto;
}
li {
  width:2rem;
  display: inline-block;
  margin: .2rem .1rem;
  text-align: center;
  vertical-align: top;
}
li img{
  width:1.8rem;
  height:2.3rem;
  box-shadow:.04rem .04rem .04rem #999;
}
.title{
    width:100%;
    margin:.24rem 0 .1rem;
    word-break:keep-all;
    white-space:nowrap;
    overflow:hidden;
    text-overflow:ellipsis;
}
.kong{width:1.1rem;height:.21rem;background: url(../../static/kong.png);display: inline-block}
.shi{width:100%;height:.21rem;background: url(../../static/shi.png);vertical-align: middle;}
.num{margin:0 0 0 .2rem;vertical-align: middle;color:#999;}
a {
  color: #000;
  text-decoration: none;
}
</style>



