<template>
  <div>
    <header-nav></header-nav>
    <div class="hello">
      <div class="hot">
        <h2>{{hots.title}}<router-link :to="{path:'/hot',query:{id:0}}"><span>更多</span></router-link></h2>
        <div class="timg-box">
          <img src="/static/timg.gif" alt="" class="timg" v-show="!hots">
        </div>
        <ul>
          <li v-for="item in hots.subjects">
              <router-link :to="{path:'/one',query:{id:item.id}}">
                <img :src="item.images.medium" alt="">
                <p class="title">{{item.title}}</p>
                <div v-show="item.rating.average==0" class="gray">暂无评分</div>
                <div class="xin-box" v-show="!item.rating.average==0">
                  <div class="kong">
                    <div :style="{width:item.rating.average*10+'%'}" class="shi"></div>
                  </div><span class="num">{{item.rating.average}}</span>
                </div>
              </router-link>
          </li>
        </ul>
      </div>
      <div class="free">
        <h2>{{frees.title}}<router-link :to="{path:'/hot',query:{id:1}}"><span>更多</span></router-link></h2>
        <div class="timg-box">
          <img src="/static/timg.gif" alt="" class="timg" v-show="!frees">
        </div>
        <ul>
          <li v-for="itemf in frees.subjects">
            <router-link :to="{path:'/one',query:{id:itemf.subject.id}}">
              <img :src="itemf.subject.images.medium" alt="">
              <p>{{itemf.subject.title}}</p>
              <div v-show="itemf.subject.rating.average==0" class="gray">暂无评分</div>
              <div v-show="!itemf.subject.rating.average==0">
                <div class="kong">
                  <div :style="{width:itemf.subject.rating.average*10+'%'}" class="shi"></div>
                </div><span class="num">{{itemf.subject.rating.average}}</span>
              </div>
            </router-link>
          </li>
        </ul>
      </div>
      <div class="new">
        <h2>{{news.title}} <router-link :to="{path:'/hot',query:{id:2}}"><span>更多</span></router-link></h2>
        <div class="timg-box">
          <img src="/static/timg.gif" alt="" class="timg" v-show="!news">
        </div>
        <ul>
          <li v-for="itemn in news.subjects">
            <router-link :to="{path:'/one',query:{id:itemn.id}}">
              <img :src="itemn.images.medium" alt="">
              <p>{{itemn.title}}</p>
              <div v-show="itemn.rating.average==0" class="gray">暂无评分</div>
              <div v-show="!itemn.rating.average==0">
                <div class="kong">
                  <div :style="{width:itemn.rating.average*10+'%'}" class="shi"></div>
                </div><span class="num">{{itemn.rating.average}}</span>
              </div>
            </router-link>
          </li>
        </ul>
      </div>
      <div class="classify">
        <h2>分类浏览</h2>
        <div>
          <router-link :to="{name:'classify',params:{id:item.name1}}" tag="li" v-for="item in id">{{item.name1}}<span>></span></router-link>
        </div>
      </div>
    </div>
  </div>
  
</template>

<script>
import headerNav from '@/components/headerNav'
export default {
  name: 'hello',
  data () {
    return {
        
        hots:'',
        frees:'',
        news:'',
        id:[
          {name1:'经典'},
          {name1:'冷门佳片'},
          {name1:'豆瓣高分'},
          {name1:'动作'},
          {name1:'喜剧'},
          {name1:'爱情'},
          {name1:'悬疑'},
          {name1:'恐怖'},
          {name1:'科幻'},
          {name1:'经典'},
          {name1:'治愈'},
          {name1:'文艺'},
          {name1:'成长'},
          {name1:'动画'},
          {name1:'华语'},
          {name1:'欧美'},
          {name1:'韩国'},
          {name1:'日本'},
        ]
    }
  },
  mounted:function(){
    this.aa();
    this.bb();
    this.cc();
    console.log('想看代码吗？求我啊')
  },
  methods:{
      aa(){
        this.$http.jsonp('https://api.douban.com/v2/movie/in_theaters').then(function(res){
            this.hots=res.data;
          },function(err){
            console.log('sb')
          })
      },
      bb(){
        this.$http.jsonp('https://api.douban.com/v2/movie/us_box').then(function(res){
            this.frees=res.data;
          },function(err){
            console.log('sb')
          })
      },
      cc(){
        this.$http.jsonp('https://api.douban.com/v2/movie/top250').then(function(res){
            this.news=res.data;
           
          },function(err){
            console.log('sb')
          })
      },
  },
  components:{
    headerNav
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hello{
  margin-top: 1.20rem;
  padding:0 .20rem;
  font-size: .28rem;
}
h2{
  font-weight:400;
  font-size:.32rem;
}
h2 span{
  float:right;
  font-size:.28rem;
  line-height:.47rem;
  color:#42bd56;
}
.gray{color:#999;}
.timg-box{
  width:100vw;
  text-align: center;
}
.timg{
  width:2.00rem;
  height:2.00rem;
}
ul {
  list-style-type: none;
  padding: 0;
  white-space: nowrap;
  width:100%;
  overflow-x: auto;
}
li {
  width:2.00rem;
  display: inline-block;
  margin: .20rem .10rem;
  text-align: center;
  vertical-align: top;
}
li img{
  width:1.80rem;
  height:2.30rem;
  box-shadow:.04rem .04rem .04rem #999;
}
.title{
    width:100%;
    margin:.24rem 0 .1rem;
    word-break:keep-all;
    white-space:nowrap;
    overflow:hidden;
    text-overflow:ellipsis;
}
.kong{width:1.1rem;height:.21rem;background: url(../../static/kong.png);display: inline-block;}
  .shi{width:100%;height:.21rem;background: url(../../static/shi.png);vertical-align: middle;}
.num{margin:0 0 0 .20rem;vertical-align: middle;color:#999;}
a {
  color: #000;
  text-decoration: none;
}
.classify li{
  margin:0;
  padding:0 .2rem;
  width: 45%;
  text-align: left;
  border-top: solid 1px #ccc;
  border-right: solid 1px #ccc;
  float: left;
  padding-right: .12rem;
  height: .8rem;
  line-height: .8rem;
  color:#42bd56;
}
.classify li span{
  float:right;
  font-size: .3rem;
  color:#999;
}
</style>



