<template>
	<div class="wrap">
		<header-nav></header-nav>
		<div class="timg-box">
	        <img src="/static/timg.gif" alt="" class="timg" v-show="datalis.length">
	    </div>
		<div v-show="!datalis.length">
			<div class="bg">
				<img src="/static/bg.jpg" alt="">
				<div class="btn">
					<button><a href="https://www.douban.com/doubanapp/app?model=B&copy=1&page=movie&channel=card_movie&direct_dl=1">极速下载</a></button>
					<button>打开</button>
				</div>
			</div>
			<h2>{{datalis.title}}</h2>
			<div class="box">
				<div class="left">
					<div v-show="parseInt(datalis.rating.average)==0"><em>评价人数不足</em></div>
					<div v-show="!parseInt(datalis.rating.average)==0">
						<div class="kong">
				            <div :style="{width:datalis.rating.average*10+'%'}" class="shi"></div>
				        </div><span class="num">{{datalis.rating.average}}<em>{{datalis.rating.numRaters}}人评价</em></span>
					</div>
					<p>	<span v-for="item in datalis.author">{{item}}/</span>
						<span v-show="datalis.publisher">{{datalis.publisher}}/</span>
						<span v-show="datalis.pages">{{datalis.pages}}页/</span>
						<span v-show="datalis.binding">{{datalis.binding}}/</span>
						<span v-show="datalis.pubdate">{{datalis.pubdate}}</span>
					</p>
				</div>
				<div class="right">
					<img :src="datalis.images.medium" alt="">
				</div>
			</div>
			<div class="prices">
				<span>在豆瓣购买</span><span>{{datalis.price}}元起</span>
			</div>
			<div class="look">
				<span>想读</span>
				<span>在读</span>
				<span>读过</span>
			</div>
			<div class="box1">
				<h3 class="gray">{{datalis.title}}的内容简介</h3>
				<p v-show="datalis.summary" class="content">{{datalis.summary}}</p>
				<p v-show="!datalis.summary" class="none">暂无内容简介</p>
			</div>
			<div class="more">
				<h3>查看更多豆瓣高分好书</h3>
				<dl>
					<dt v-for="item in datalis.tags">{{item.title}}</dt>
				</dl>
			</div>
		</div>
	</div>
</template>

<script>
import headerNav from '../components/headerNav'
export default {
	name:'details',
	data(){
		return {
			id:this.$route.query.id,
			datalis:[]
		}
	},
	mounted:function(){
		this.get()
	},
	methods:{
		get(){
			this.$http.jsonp('https://api.douban.com/v2/book/'+this.$route.params.id).then(function(res){
				console.log(res.data)
				this.datalis=res.data
			},function(){
				console.log('sb')
			})
		}
	},
	components:{
		headerNav,
	}
}
</script>
<style scoped>
	.gray{
		color:#999 !important;
		font-size: .26rem;
	}
	.wrap{
		overflow: hidden;
		font-size: .26rem;
	}
	.timg-box{
	  width:100vw;
	  position: absolute;
	  top:50%;
	  transform: translateY(-50%);
	  text-align: center;
	}
	.timg{
	  width:2rem;
	  height:2rem;
	}
	.bg{
		position: relative;
		margin-top:.8rem;
	}
	.bg img{
		position: relative;
		height:1.5rem;
		width:100%;
	}
	.bg .btn{
		position: absolute;
		width:100vw;
		/*margin:.25rem 0;*/
		top:40%;
		text-align: right;
	}
	.bg button{
		height:.5rem;
		margin:0 .08rem;
		font-size:.26rem;
		border-radius:.1rem;
		border:none;
		outline:none;
	}
	.bg button:nth-of-type(1){
		width:1.2rem;
		background: #42bd56;
		color:white;
		font-weight: 900;
	}
	.bg button:nth-of-type(2){
		width:1rem;
		margin-right:.3rem;
		border:1px solid #42bd56;
		color:#42bd56;
		background: transparent;
	}
	h2,h3{
		font-weight: 400;
	}
	h2{
		font-size: .3rem !important;
		padding:0 .2rem;
	}
	.box{
		padding:0 .1rem;
		font-size:.28rem;
	}
	.kong{width:1.1rem;height:.21rem;background: url(../../static/kong.png);display: inline-block;}
	.shi{width:100%;height:.21rem;background: url(../../static/shi.png);vertical-align: middle;}
	.num{margin:0 0 0 .2rem;vertical-align: middle;color:#999;}
	em{margin:0 .2rem;color:#999;font-size: .24rem;}
	.app{color:#2fc5c5;}
	.box .left{
		width:42vw;
		float:left;
		padding:0 .2rem;
	}
	.box .left p{
		font-size: .26rem;
	}
	.box .right{
		padding-right:.45rem;
		float:right;
	}
	.box .right img{
		height:2.4rem;
		width:1.9rem;
	}
	.box:after{
		content: '';
		display: block;
		clear:both;
	}
	.prices{
		padding:.2rem .1rem;
		margin:.2rem 0;
		border-top:1px solid #999;
		border-bottom:1px solid #999;
	}
	.prices span:nth-of-type(1){
		font-size: .26rem;
		color:#42bd56;
	}
	.prices span:nth-of-type(2){
		font-size: .26rem;
		color:#999;
		float:right;
	}
	.look{
		margin:.4rem 0;
		text-align: center;
	}
	.look span{
		display: inline-block;
	    height: .5rem;
	    width:29vw;
	    line-height:.5rem;
	    border:1px solid #ffb712;
	    border-radius:. 3rem;
	    margin-right: .1rem;
	    color: #ffb712;
	    font-size: .26rem;
	    text-align: center;
	}
	.box1{
		padding:0 .2rem;
	}
	.box1 p{text-indent: .4rem;}
	h4{padding:0 .2rem;}
	ul{
		width:100vw;
		white-space: nowrap;
		overflow-x: auto;
		padding:0;
	}
	.content{font-size: .26rem;}
	.none{text-align: center;font-size: .26rem;color:#999;}
	.more h3{padding:0 .2rem;font-size: .26rem}
	.more dl{padding:0 .2rem;}
	.more dt{
		display: inline-block;
		padding:.1rem .3rem;
		margin:.2rem;
		background-color:#ddd;
		border-radius:.2rem;
		font-size: .28rem;
		color:#494949;
	}
	li{
		list-style: none;
		display: inline-block;
		text-align: center;
		margin:.1rem .2rem;
		
	}
	a{
		color:white;
		text-decoration: none;
	}
</style>


