					people:9117,
					add:'申请加入',
				},
				{
					img:'https://img3.doubanio.com/icon/g21714-1.jpg',
					title:'恋爱 婚姻 家庭',
					people:49897,
					add:'加入',
				},
				{
					img:'https://img3.doubanio.com/icon/g37539-6.jpg',
					title:'我爱代购',
					people:258388,
					add:'加入',
				},
				{
					img:'https://img3.doubanio.com/icon/g14954-23.jpg',
					title:'代购之家',
					people:501843,
					add:'加入',
				},
				{
					img:'https://img3.doubanio.com/icon/g571586-16.jpg',
					title:'这个小组不太冷',
					people:3024,
					add:'加入',
				},
				{
					img:'https://img3.doubanio.com/icon/g300203-15.jpg',
					title:'北京租房',
					people:37110,
					add:'加入',
				},
				{
					img:'https://img3.doubanio.com/icon/g10021-3.jpg',
					title:'摄影',
					people:224076,
					add:'加入',
