<style scoped>
	*{margin:0;padding:0;}
	.tit-img{
		margin-top:1.06rem;
		padding:.5rem .2rem;
	}
	.tit-img img{
		width:44vw;
	}
	.tit-img img:nth-of-type(1){
		margin-right:.2rem;
	}
	.box{padding: 0 .2rem;}
	.box ul li{
		display: inline-block;
		height:.7rem;
		width:42vw;
		margin:.1rem;
		font-size: .24rem;
		line-height: .7rem;
		text-align: center;
		list-style: none;
		background: #eee;
		border-radius: .1rem;
	}
	.my-group{
		margin:.4rem 0 .3rem 0;
