	}
	li .name p:nth-of-type(2){
		font-size: .2rem;
		color:#999;
	}
	li .btn{
		position: absolute;
		padding:.1rem .2rem;
		border:1px solid #42bd56;
		border-radius: .1rem;
		font-size:.24rem;
		color:#42bd56;
		top:.25rem;
		right:.5rem;
	}
</style>


